* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	background-image: linear-gradient(120deg, #c33eea, #69bbf1);
	color: white;
	font-family: "Poppins", sans-serif;
	min-height: 100vh;
}

header {
	font-size: 1.5rem;
}

header,
form {
	min-height: 20vh;
	display: flex;
	justify-content: center;
	align-items: center;
}

	form input, form button {
		padding: 0.5rem;
		font-size: 2rem;
		border: none;
		background: white;
	}

	form button {
		color: cornflowerblue;
		background: white;
		cursor: pointer;
		transition: all 0.3s ease;
	}

		form button:hover {
			color: white;
			background: cornflowerblue;
		}

.todo-container {
	display: flex;
	justify-content: center;
	align-items: center;
}

.todo-list {
	min-width: 30%;
	list-style: none;
}

.todo {
	margin: 0.5rem;
	background: white;
	color: black;
	font-size: 1.5rem;
	display: flex;
	justify-content: space-between;
	align-items: center;
	transition: all 0.5s ease;
}

	.todo li {
		flex: 1;
		margin-left: 0.5rem;
		margin-right: 0.5rem;
	}

.trash-btn, .complete-btn {
	background: #5ad1fe;
	color: white;
	border: none;
	padding: 1rem;
	cursor: pointer;
	font-size: 1rem;
}

.complete-btn {
	background: #5afe87;
}

.trash-btn:hover {
	background: #73d8fe;
	
}

.complete-btn:hover {
	background: #8dfeac;
}

.fa-trash-can, .fa-circle-check {
	pointer-events: none;
}

.completed {
	text-decoration: line-through;
	opacity: 0.5;
}

.fall {
	transform: translateY(8rem) rotateZ(20deg);
	opacity: 0;
}

select {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	outline: none;
	border: none;
}

.select {
	margin: 1rem;
	position: relative;
	overflow: hidden;
}

select {
	color: darkorchid;
	width: 10rem;
	cursor: pointer;
	padding: 1rem;
}

.select::after {
	content: "\25BC";
	position: absolute;
	background: cornflowerblue;
	top: 0;
	right: 0;
	padding: 0.8rem;
	pointer-events: none;
}

.select:hover::after {
	color: cornflowerblue;
	background: white;
}
